<script>
import { reactive, ref } from 'vue'
import { onMounted, toRef } from 'vue'
import NewsEntry from './TheNewsFeedEntry.vue';
import TheNewsFeedGoalEntry from './TheNewsFeedGoalEntry.vue';
import TheNewsFeedCalendarEntry from './TheNewsFeedCalendarEntry.vue';
import TheNewsFeedMemberEntry from './TheNewsFeedMemberEntry.vue';
import TheNewsFeedQuestionEntry from './TheNewsFeedQuestionEntry.vue';
import axios from 'axios'

export default {
  props: {
    circleid: Number,
  },
  components: { NewsEntry, TheNewsFeedGoalEntry, TheNewsFeedCalendarEntry, TheNewsFeedMemberEntry, TheNewsFeedQuestionEntry },
  setup(props) {
    onMounted(() => { fetchNews() })
    const cid = toRef(props, 'circleid')
    const state = reactive({
      cid: cid,
      lastDoneGoalDTO: '',
      newestRegdateGoalDTO: '',
      lastDoneCalendarDTO: '',
      newestCircleMemberDTO: '',
      xNewestQuestionDTO: '',
    });
    const fetchNews = async () => {
      console.log("state.cid :" + state.cid)

      console.log("loginHandler launch")
      const url = "http://43.201.60.125/api/newsfeed"
      const headers = {
        //TODO send the right amound of tokent and receive the same...
        'Content-Type': 'application/json',
        Authorization: sessionStorage.getItem('TOKEN'),
        token: sessionStorage.getItem('TOKEN'),
      }
      //set cid
      const body = { cid: state.cid }
      await axios.post(url, body, { headers }).then(function (res) {
        // console.log("the res lastDoneGoalDTO :"+res.data.lastDoneGoalDTO)
        // console.log("the res newestRegdateGoalDTO :"+res.data.newestRegdateGoalDTO)
        // console.log("the res :"+res.data.lastDoneCalendarDTO)
        // console.log("the res :"+res.data.newestCircleMemberDTO)
        // console.log("the res :"+res.data.xNewestQuestionDTO)
        state.lastDoneGoalDTO = res.data.lastDoneGoalDTO
        state.newestRegdateGoalDTO = res.data.newestRegdateGoalDTO
        state.lastDoneCalendarDTO = res.data.lastDoneCalendarDTO
        state.newestCircleMemberDTO = res.data.newestCircleMemberDTO
        state.xNewestQuestionDTO = res.data.xNewestQuestionDTO
      }).catch(() => { console.log("there was an error") })
    }
    return { state, fetchNews }
  }
}
</script>

